<template>
  <div class="store-shelf">
    <ShelfTitle :shelfTitle="shelfTitle" :ifShowBack="true" />
    <Scroll
      class="store-shelf-scroll-wrapper"
      @onScroll="onScroll"
      :bottom="scrollBottom"
      ref="scroll"
    >
      <ShelfList class="shelf-list" :data="shelfCategory.itemList" />
    </Scroll>
    <ShelfFooter />
  </div>
</template>

<script>
import ShelfTitle from '../../components/shelf/ShelfTitle'
import Scroll from '../../components/common/Scroll.vue'
import ShelfList from '../../components/shelf/ShelfList'
import { storeShelfMixin } from '../../utils/mixin'
import ShelfFooter from '../../components/shelf/ShelfFooter'
export default {
  mixins: [storeShelfMixin],
  components: {
    ShelfTitle,
    Scroll,
    ShelfList,
    ShelfFooter,
  },
  watch: {
    isEditMode(v) {
      this.scrollBottom = v ? 48 : 0
      this.$nextTick(() => {
        this.$refs.scroll.refresh()
      })
    },
  },
  computed: {
    shelfTitle() {
      // return this.$router.params
      return this.getTitle ? this.getTitle : this.$t('shelf.title')
    },
    getTitle() {
      return this.$route.query.title
    },
  },
  data() {
    return {
      scrollBottom: 0,
    }
  },
  mounted() {
    this.getCategoryList(this.$route.query.title)
    this.setCurrentType(2)
    // console.log(this.shelfCategory)
  },
  methods: {
    onScroll(v) {
      this.setOffsetY(v)
      // console.log(v)
    },
  },
}
</script>
